    <div class="text-right py-3">
      <button class="btn btn-large btn-secondary" @click="openModal(true)">æ°å»ºCoupon</button>
    </div>
    <table class="table">
      <thead class="thead-light">
        <tr>
          <th scope="col">Couponåç¨±</th>
          <th scope="col" width="200">Coupon Code</th>
          <th scope="col" width="110">æªæ­¢æ¥</th>
          <th scope="col" width="80">ææ¸</th>
          <th scope="col" width="130">ç·¨è¼¯</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in coupons" :key="item.id">
          <td>{{item.title}}</td>
          <td>{{item.code}}</td>
          <td>{{item.due_date|format-time}}</td>
          <td>
            {{item.percent}}
            <!-- <span v-if="item.is_enabled">å·²åç¨</span>
            <span v-if="!item.is_enabled">æªåç¨</span>-->
          </td>
          <td>
            <a
              class="btn btn-outline-primary btn-sm"
              href="#"
              @click.prevent="openModal(false,item)"
            >ç·¨è¼¯</a>
            <a
              class="btn btn-outline-danger btn-sm"
              href="#"
              @click.prevent="openModal(false,item,true)"
            >åªé¤</a>
          </td>
        </tr>
      </tbody>
    </table>
    <pagination :pagination="pagination" @gopage="getCouponsData"></pagination>
    <div
      class="modal fade"
      id="couponModal"
      tabindex="-1"
      role="dialog"
      aria-labelledby="exampleModalLabel"
      aria-hidden="true"
    >
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span v-if="isNew">æ°å¢Coupon</span>
              <span v-if="!isNew">ç·¨è¼¯Coupon</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div class="form-group">
                  <label for="title">æ¨é¡</label>
                  <input
                    type="text"
                    class="form-control"
                    id="title"
                    placeholder="è«è¼¸å¥æ¨é¡"
                    v-model="tempCoupon.title"
                  />
                </div>

                <div class="form-row">
                  <div class="form-group col-md-12">
                    <label for="category">ææ¸</label>
                    <input
                      type="number"
                      class="form-control"
                      id="category"
                      placeholder="è«è¼¸å¥ææ¸"
                      v-model="tempCoupon.percent"
                    />
                  </div>
                  <div class="form-group col-md-12">
                    <label for="price">ææ£ç¢¼</label>
                    <input
                      type="unit"
                      class="form-control"
                      id="unit"
                      placeholder="è«è¼¸å¥ææ£ç¢¼"
                      v-model="tempCoupon.code"
                    />
                  </div>
                  <div class="form-group col-md-12">
                    <label for="origin_price">å°ææ¥</label>
                    <input
                      type="date"
                      class="form-control"
                      id="origin_price"
                      placeholder="è«è¼¸å¥å°ææ¥"
                      v-model="tempCoupon.due_date"
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">åæ¶</button>
            <button type="button" class="btn btn-primary" @click="updateCoupon">
              <i class="fas fa-spinner fa-spin" v-if="isLoading"></i>ç¢ºèª
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="modal fade" id="delcouponModal" tabindex="-1" role="dialog">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="delcouponModalLabel">
              <span>åªé¤Coupon</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            æ¯å¦åªé¤ {{tempCoupon.title}} Couponã
            <strong class="text-danger">(åªé¤å¾å°ç¡æ³æ¢å¾©)</strong>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal">åæ¶</button>
            <button type="button" class="btn btn-danger" @click="removeCoupon">ç¢ºèªåªé¤</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
import pagination from '../components/Pagination'
export default {
  data () {
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      tempTimestamp: ''
    }
  },
  methods: {
    getCouponsData (page = 1) {
      let vm = this
      vm.$store.dispatch('updateLoading', true)
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`
      vm.$http.get(api).then(response => {
        console.log('å¾å°åå¾Couponsè³æï¼', response)
        vm.coupons = response.data.coupons
        vm.pagination = response.data.pagination
        vm.$store.dispatch('updateLoading', false)
      })
      // å°ç¾æçåé è³æå©ç¨propså³å°åä»¶ä¸­ï¼åå©ç¨emitå°é»ææè§¸ç¼çè³æåå³å°æ­¤é é¢ä¸­ï¼éæ¨£å°±å¯ä»¥å©ç¨è³æéæ°AJAXå¾å°æ°çä¸ç­åé è³æ
      // å½å¼ä¸­åé è¨­è®æ¸å¸¶å¥1
    },
    updateCoupon () {
      let vm = this
      vm.$store.dispatch('updateLoading', true)
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`
      let method = 'post'
      if (!vm.isNew) {
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
        method = 'put'
      }
      vm.formatTimestamp(vm.tempCoupon.due_date)
      const coupon = vm.tempCoupon
      vm.$http[method](api, { data: coupon }).then(response => {
        if (!response.data.success) {
          console.log('æ´æ°couponå¤±æ', response)
          vm.$store.dispatch('updateLoading', false)
          $('#couponModal').modal('hide')
        } else {
          console.log('updateCoupon:', response)
          vm.$store.dispatch('updateLoading', false)
          $('#couponModal').modal('hide')
          vm.getCouponsData()
        }
      })
    },
    removeCoupon () {
      let vm = this
      vm.$store.dispatch('updateLoading', true)
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
      vm.$http.delete(api).then(response => {
        console.log('deleteCoupons:', response)
        vm.$store.dispatch('updateLoading', false)
        if (!response.data.success) {
          console.log(response.data.message)
        }
        $('#delcouponModal').modal('hide')
        vm.getCouponsData()
      })
    },
    openModal (isNew, item, isDel) {
      if (isNew) {
        this.tempCoupon = {}
        this.tempTimestamp = ''
        this.$store.dispatch('updateisNew', true)
      } else if (!isNew && isDel) {
        this.tempCoupon = Object.assign({}, item)
        $('#delcouponModal').modal('show')
        return
      } else {
        this.tempCoupon = Object.assign({}, item)
        this.formatTime(this.tempCoupon.due_date)
        this.$store.dispatch('updateisNew', false)
      }
      $('#couponModal').modal('show')
    },
    formatTime (timestamp) {
      let dates = new Date(timestamp * 1000)
      let year = dates.getFullYear()
      let month =
        dates.getMonth() < 10
          ? `0${dates.getMonth() + 1}`
          : dates.getMonth() + 1
      let date = dates.getDate() < 10 ? `0${dates.getDate()}` : dates.getDate()
      this.tempCoupon.due_date = `${year}-${month}-${date}`
      console.log('formatTime:', this.tempCoupon.due_date)
    },
    formatTimestamp (date) {
      let dates = new Date(date)
      this.tempCoupon.due_date = Math.floor(dates.getTime() / 1000)
      console.log('formatTimestamp:', this.tempCoupon.due_date)
    }
  },
  computed: {
    isLoading () {
      return this.$store.state.isLoading
    },
    isNew () {
      return this.$store.state.isNew
    }
  },
  created () {
    this.getCouponsData()
  },
  components: {
    pagination
  }
}
</script>
